.banner {
  background-color: #333;
  color: #fff;
  height: calc(100vh - 109px);
  padding: 20px;
  background-size: cover;
  position: relative;
  display: grid;
  place-items: center;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.content {
  position: relative;
  z-index: 2;
  color: #fff;
  text-align: center;
  font-family: "Archivo Black", sans-serif;
}

.content h1 {
  font-size: 45px;
}

@media (max-width: 500px) {
  .content h1 {
    font-size: 30px;
  }
}

.content h4 {
  font-size: 20px;
}

@media (max-width: 500px) {
  .content h4 {
    font-size: 16px;
  }
}
